<template>
    <div id="werknemer-bekijk">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>{{naam}}</h4>
            </li>
            <li class="collection-item">Nummer Werknemer :{{werknemer}} </li> 
            <li class="collection-item">Department : {{dept}}</li>
            <li class="collection-item">Positie : {{positie}}</li>
        </ul>
        <router-link to="/" class="btn grey">Back</router-link>
        <button @click="deleteEmployee" class="btn red">Delete</button>
        <div class="fixed-action-btn">
        <router-link v-bind:to="{name: 'werknemer-bewerk', params: {werknemer: werknemer}}" class="btn-floating btn-large red">
        <i class="fa fa-pencil"></i>
        </router-link>
        </div> 
    </div>
</template>
<script>
import db from './firebaseInit'
export default {
    name: 'werknemer-bekijk',
    data () {
    return{
        werknemer: null,
        naam: null,
        dept: null,
        positie: null
    }
    },
    beforeRouteEnter(to, from, next) {
        db.collection('werknemers').where('werknemer', '==', to.params.werknemer).get()
            .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.werknemer = doc.data().werknemer
                    vm.naam = doc.data().naam
                    vm.dept = doc.data().dept
                    vm.positie = doc.data().positie
                })
            })
        })
    },
watch: {
    '$route' : 'fetchData'
},
    methods: {
        fetchData() {
            db.collection('werknemers').where(
            'werknemer', '==',
            this.$route.params.werknemer).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.werknemer = doc.data().werknemer
                    this.naam = doc.data().naam
                    this.dept = doc.data().dept
                    this.positie = doc.data().positie
                })
            })
        },
        deleteEmployee () {
            if(confirm('Bent u zeker ?')) {
                db.collection('werknemers').where('werknemer', '==',
                this.$route.params.werknemer).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete()
                        this.$router.push('/')
                    })
                 })
            }
        }
    }
}
</script>


