import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import NieuweWerknemer from '@/components/NieuweWerknemer'
import BekijkWerknemer from '@/components/BekijkWerknemer'
import BeWerknemer from '@/components/BeWerknemer'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
      {
      path: '/new',
      name: 'werknemer-nieuw',
      component: NieuweWerknemer
    },
      {
      path: '/edit/:werknemer',
      name: 'werknemer-bewerk',
      component: BeWerknemer
    },
      {
      path: '/:werknemer',
      name: 'werknemer-bekijk',
      component: BekijkWerknemer
    }
  ]
})



