<template>
    <div id="werknemer-nieuw">
      <h3>Nieuwe werknemer</h3>
        <div class="row">
            <form @submit.prevent="saveWerknemer" class="cols12">
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="werknemer" required>
                    <label>Werknemer nummer</label>
                </div>
                
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="naam" required>
                    <label>Naam</label>
                </div>
            </div>
                <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="dept" required>
                    <label>Department</label>
                </div>
            </div>
                <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="positie" required>
                    <label>Positie</label>
                </div>
            </div>
                <button type="submit" class="btn">Voeg toe</button>
                <router-link to="/" class="btn grey">Lotmor</router-link>
            </form>
        </div>
    </div>
</template>

<script>
    import db from './firebaseInit'
export default {
    name: 'werknemer-nieuw',
    data () {
    return{
        werkgever: null,
        naam: null,
        dept: null,
        positie: null
    
    }
  },
    methods: {
        saveWerknemer() {
            db.collection('werknemers').add({
                werknemer: this.werknemer,
                naam: this.naam,
                dept: this.dept,
                positie: this.positie
            })
            .then(docRef => this.$router.push('/'))
            .catch(error => console.log(err))
        }
    }
}
</script>


