<template>
    <div id="dashboard">
      <ul class="collection with-header">
        <li class="collection-header">
            <h4>Werknemers</h4>
    
        </li>
        <li v-for="employee in employees"
        v-bind:key="employee.id"
        class="collection-item">
            <div class="chip">{{employee.dept}}</div>
            {{employee.werknemer}}:{{employee.naam}}
            
            <router-link class="secondary-content" 
            v-bind:to="{name: 'werknemer-bekijk', params: {werknemer: employee.werknemer}}">
    <i class="fa fa-eye"></i>
    </router-link>
            
        </li>
        </ul>
        
        <div class="fixed-action-btn">
        <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
        </router-link>
        </div> 
        
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'dashboard',
    data () {
    return{
        employees: []
    }
    },
    created () {
        db.collection('werknemers').get().then
        (querySnapshot =>{
        querySnapshot.forEach(doc => {
            console.log(doc.id);
        const data = {
           'id': doc.id,
            'werknemer': doc.data().werknemer,
            'naam': doc.data().naam,
            'dept': doc.data().dept,
            'positie': doc.data().positie 
        }
        this.employees.push(data)
        })
        }
        
        )
    }
}
</script>


