<template>
    <div id="werknemer-bewerk">
        <h3>Bewerk werknemer</h3>
        
        <div class="row">
            <form @submit.prevent="updateWerknemer" class="cols12">
            <div class="row">
                <div class="input-field col s12">
                    <input disabled type="text" v-model="werknemer" required>
                    
                </div>
                
            </div>
            <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="naam" required>
                    
                </div>
            </div>
                <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="dept" required>
                    
                </div>
            </div>
                <div class="row">
                <div class="input-field col s12">
                    <input type="text" v-model="positie" required>
                    
                </div>
            </div>
                <button type="submit" class="btn">Voeg toe</button>
                <router-link to="/" class="btn grey">Lotmor</router-link>
            </form>
        </div>
    </div>
</template>

<script>
    import db from './firebaseInit'
export default {
    name: 'werknemer-bewerk',
    data () {
        return{
            werknemer: null,
            naam: null,
            dept: null,
            positie: null
            }
        },
        beforeRouteEnter(to, from, next) {
        db.collection('werknemers').where('werknemer', '==', to.params.werknemer).get()
            .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.werknemer = doc.data().werknemer
                    vm.naam = doc.data().naam
                    vm.dept = doc.data().dept
                    vm.positie = doc.data().positie
                })
            })
        })
    },
watch: {
    '$route' : 'fetchData'
},
    methods: {
        fetchData() {
            db.collection('werknemers').where(
            'werknemer', '==',
            this.$route.params.werknemer).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.werknemer = doc.data().werknemer
                    this.naam = doc.data().naam
                    this.dept = doc.data().dept
                    this.positie = doc.data().positie
                })
            })
        },
        updateWerknemer () {
             db.collection('werknemers').where(
            'werknemer', '==',
            this.$route.params.werknemer).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        werknemer: this.werknemer,
                        naam: this.naam,
                        dept: this.dept,
                        positie: this.positie
                    })
                    .then(() => {
                        this.$router.push({name: 'werknemer-bekijk', params: {werknemer: this.werknemer}})
                    })
                })
            })
            
        }
        
    }
}
</script>


